import { Inline, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import Sticker from '../models';

export default {
  name: 'sticker',

  match(node: Node): boolean {
    return Sticker.isSticker(node);
  },

  convert(node: Inline): JsonML {
    return ['span', { ...node.data, 'data-type': Sticker.TYPE }];
  },
};
